<template>
  <div class="container">
    <div class="diary">
      <div class="row diaries">
        <div class="col-lg-8 diaries-right" >
          <ul>
            <li v-for="item in diaries" class="single-diary">
              <p>{{item.Content}}</p>
              <div class="other">
                <span>测试Id：{{item.Id}}</span>
                <span>时间：{{item.Time | formatDate}}</span>
                <span>星期：{{item.Week}}</span>
                <span>天气：{{item.Weather}}</span>
                <span>心情：{{item.Mood}}</span>
              </div>
            </li>
          </ul>
          <!--分页-->
          <div class="pages">
            <a class="first-page">首页</a>
            <a class="page-count"  v-on:click="jumpToPage(currentPage)">上一页</a>
            <a class="page-count"  v-for="index in indexs" v-on:click="jumpToPage(index)">{{index}}</a>
            <a class="page-count"  v-on:click="jumpToPage(currentPage + 2)">下一页</a>
            <a class="first-page">末页</a>
          </div>
          <!--分页结束-->
        </div>
        <div class="col-lg-4 diaries-left">右侧</div>
      </div>
    </div>


  </div>
</template>

<script>
  import {formatDate} from '../common/date'
  export default {
    name: 'diary',
    data () {
      return {
        diaries: '',
        indexs: 3,
        currentPage: []
      }
    },
    mounted: function () {
      this.$http.get('/api/Diary/GetDiaries?pageIndex=0&pageSize=3').then(response => {
        this.diaries = response.data
      })
    },
    filters: {
      formatDate (time) {
        let date = new Date(time)
        return formatDate(date, 'yyyy-MM-dd hh:mm')
      }
    },
    methods: {
      jumpToPage: function (index) {
        this.$http.get('/api/Diary/GetDiaries', {params: {pageIndex: index - 1, pageSize: 3}}).then(response => {
          this.currentPage = (index - 1)
          this.diaries = response.data
          console.log(this.currentPage)
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .single-diary{ display: block;margin-top:30px;}
  .single-diary p{text-indent: 2em;text-align: left; line-height: 20px;}
  .single-diary .other{text-align: right;}
 .single-diary .other span{margin-left: 20px;}
  .pagges a{display: inline-block;}
</style>



